html, body{
  margin: 0;
  padding: 0;
  height: 100%
}

html {
  /*background: url(./standpeople.jpg) no-repeat center center fixed;*/
  background-size: cover;
}

* {
  font-family: sans-serif;
}

.TopicBox {
  background-color: pink;
  border: 2px solid black;
  width: 30%;
}

.homeNavBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: #E99488;
}

.homeNavBar h2 {
  color: #272838;
}

.right {
 right: 0;
}

.button {
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
   margin-right: 25px;
  -webkit-transition-duration: 0.4s; /* Safari */
  -o-transition-duration: 0.4s;
     transition-duration: 0.4s;
  cursor: pointer;
  background-color: white;
  color: black;
  border: 2px solid #7E8099;
}

.button:hover {
    background-color: #7E8099;
    color: white;
}

.button a {
  color: #7E8099;
  text-decoration: none;
}

.button a:hover{
  color: white;
}



